"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  PieChart,
  Pie,
  Cell,
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";

// Bot Behavior data
const botBehaviorData = [
  { name: "Desktop", value: 20 },
  { name: "Mobile", value: 80 },
];

const COLORS = ["#0088FE", "#FF8042"];

// Repeat IPs data
const repeatIpsData = [
  { name: "2409.4a90-at-dkv3:bc00:", totalErrors: 250, totalVisits: 180 },
  { name: "2401.4900:6401:8fad:17eb:bcb4:43b1:6621", totalErrors: 200, totalVisits: 150 },
  { name: "2409.d565-tc:010-017c:8baa:3971:c892", totalErrors: 180, totalVisits: 130 },
  { name: "2401.4900:3adc:aca6:dc3c:18f8:7f7fd8:", totalErrors: 160, totalVisits: 120 },
  { name: "2409.4bd4:6d:f32:b620:bf6:8c6:4271", totalErrors: 140, totalVisits: 100 },
  { name: "2409.40a4:dce:198:6be6:4d8c:41d:f870", totalErrors: 120, totalVisits: 80 },
  { name: "2401.4900-at-dkv3:bc00:", totalErrors: 100, totalVisits: 60 },
  { name: "2401.4900:6401:8fad:17eb:bcb4:43b1:6621", totalErrors: 80, totalVisits: 40 },
];

// Hardware Concurrency data
const hwConcurrencyData = [
  { name: "HC 1", mobile: 100, desktop: 0 },
  { name: "HC 2", mobile: 100, desktop: 0 },
  { name: "HC 3", mobile: 100, desktop: 0 },
  { name: "HC 4", mobile: 95, desktop: 5 },
  { name: "HC 8", mobile: 80, desktop: 20 },
  { name: "HC 12", mobile: 85, desktop: 15 },
  { name: "HC 16", mobile: 90, desktop: 10 },
];

export function InvalidTrafficAnalysis() {
  return (
    <div>
      <div className="bg-gray-950 border border-gray-800 py-2 px-4 rounded-sm text-center">
        <h2 className="text-lg font-semibold">Invalid Traffic (SIVT)</h2>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mt-6">
        <Card className="bg-gray-950 border-gray-800 col-span-1 md:col-span-1 row-span-2">
          <CardHeader className="pb-2">
            <CardTitle className="text-sm">Device Signature</CardTitle>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead className="text-xs">Device Signature</TableHead>
                  <TableHead className="text-xs">Publisher Name</TableHead>
                  <TableHead className="text-xs">Sub Publisher</TableHead>
                  <TableHead className="text-xs">Visit</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {Array(8).fill(0).map((_, i) => (
                  <TableRow key={i}>
                    <TableCell className="text-xs"></TableCell>
                    <TableCell className="text-xs"></TableCell>
                    <TableCell className="text-xs"></TableCell>
                    <TableCell className="text-xs"></TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>

        <div className="grid grid-cols-1 gap-6">
          <Card className="bg-gray-950 border-gray-800">
            <CardHeader className="pb-2">
              <CardTitle className="text-sm">User Behavior Flags</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="bg-yellow-400/20 px-3 py-2 rounded text-center">
                <span className="text-yellow-400 font-semibold text-sm">Mouse Movement: FALSE</span>
              </div>
              <div className="bg-yellow-400/20 px-3 py-2 rounded text-center">
                <span className="text-yellow-400 font-semibold text-sm">Touch support: FALSE</span>
              </div>
              <div className="bg-yellow-400/20 px-3 py-2 rounded text-center">
                <span className="text-yellow-400 font-semibold text-sm">HasFocus = FALSE</span>
              </div>
            </CardContent>
          </Card>

          <Card className="bg-gray-950 border-gray-800">
            <CardHeader className="pb-2">
              <CardTitle className="text-sm">Bot Behaviour %</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="h-[200px] w-full">
                <ResponsiveContainer width="100%" height="100%">
                  <PieChart>
                    <Pie
                      data={botBehaviorData}
                      innerRadius={60}
                      outerRadius={80}
                      fill="#8884d8"
                      paddingAngle={5}
                      dataKey="value"
                    >
                      {botBehaviorData.map((entry, index) => (
                        <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                      ))}
                    </Pie>
                    <Tooltip
                      contentStyle={{
                        backgroundColor: "#111",
                        border: "1px solid #333",
                        borderRadius: "4px",
                      }}
                    />
                    <Legend />
                  </PieChart>
                </ResponsiveContainer>
              </div>
            </CardContent>
          </Card>
        </div>

        <Card className="bg-gray-950 border-gray-800">
          <CardHeader className="pb-2">
            <CardTitle className="text-sm">Repeat IPs</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="h-[200px] w-full">
              <ResponsiveContainer width="100%" height="100%">
                <BarChart
                  data={repeatIpsData}
                  layout="vertical"
                  margin={{
                    top: 5,
                    right: 30,
                    left: 20,
                    bottom: 5,
                  }}
                >
                  <XAxis type="number" tick={{ fontSize: 10 }} stroke="#888" />
                  <YAxis dataKey="name" type="category" scale="band" tick={{ fontSize: 8 }} width={150} stroke="#888" />
                  <CartesianGrid strokeDasharray="3 3" horizontal={false} stroke="#444" />
                  <Tooltip
                    contentStyle={{
                      backgroundColor: "#111",
                      border: "1px solid #333",
                      borderRadius: "4px",
                    }}
                  />
                  <Legend />
                  <Bar dataKey="totalErrors" name="Total Errors" fill="#FF8042" />
                  <Bar dataKey="totalVisits" name="Total Visit" fill="#8884d8" />
                </BarChart>
              </ResponsiveContainer>
            </div>
          </CardContent>
        </Card>

        <Card className="bg-gray-950 border-gray-800">
          <CardHeader className="pb-2">
            <CardTitle className="text-sm">Hardware Concurrency %</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="h-[200px] w-full">
              <ResponsiveContainer width="100%" height="100%">
                <BarChart
                  data={hwConcurrencyData}
                  margin={{
                    top: 5,
                    right: 30,
                    left: 20,
                    bottom: 5,
                  }}
                  layout="vertical"
                  stackOffset="expand"
                >
                  <XAxis type="number" tick={{ fontSize: 10 }} stroke="#888" />
                  <YAxis dataKey="name" type="category" scale="band" tick={{ fontSize: 10 }} stroke="#888" />
                  <Tooltip
                    contentStyle={{
                      backgroundColor: "#111",
                      border: "1px solid #333",
                      borderRadius: "4px",
                    }}
                  />
                  <Legend />
                  <Bar dataKey="mobile" name="Mobile" stackId="a" fill="#FF8042" />
                  <Bar dataKey="desktop" name="Desktop" stackId="a" fill="#0088FE" />
                </BarChart>
              </ResponsiveContainer>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
} 